
.group.file-type {
  margin-bottom: 16px;
  position: relative;
  transition: all .3s;
}
.file-type label {
  width: 100%;
  text-align: left;
  padding-right: 0;
  line-height: 22px;
  font-size: .875rem;
  color: #39373c!important;
  display: block;
}
.file-item {
  width: 100%;
  display: inline-block;
  vertical-align: middle;
  position: relative;
}
input[type=file]+label {
  border: 2px dashed #dee4f0;
  background: #f5f6fa;
  border-radius: 4px;
  padding: 8px 24px;
  text-align: center;
  color: #666;
  font-size: 13px;
  box-sizing: border-box;
  cursor: pointer;
  outline: 0;
  font-weight: 400;
  letter-spacing: -.1px;
  pointer-events: none;
}
input[type=file] {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
  z-index: 0;
}

input.form-file-upload {
  width: 100%;
  font-size: .929rem;
  color: #191b22;
  outline: 0;
  box-sizing: border-box;
  padding: 6px 10px 7px;
  box-shadow: none;
  vertical-align: top;
  height: auto;
  line-height: 23px;
  font-weight: 400;
  background: #fff;
  border: 1px solid #dee4f0;
  border-radius: 4px;
}
.file-item label {
  display: inline-block;
  font-size: .93rem;
  padding-right: 20px;
  text-align: right;
  box-sizing: border-box;
  width: 40%;
  vertical-align: top;
  color: #646d8c;
  position: relative;
  line-height: 20px;
  vertical-align: middle;
  word-break: break-word;
  width: 100%;
  text-align: left;
  padding-right: 0;
  line-height: 22px;
  font-size: .875rem;
  color: #39373c!important;
}